/* ------------------------------------------------*/
/*  Responsive CSS
/* ------------------------------------------------*/
/**
 * Table of Contents
 *
 * 1.0 - Desktop - Large Devices (1199px)
 * 2.0 - Tablets - Medium Devices (991px/768px)
 * 3.0 - Phones - Small Devices (480px)
 * 4.0 - Retina Devices
 */
@media (max-width: 1199px) {
  canvas {
    max-width: 100%;
    height: auto !important;
  }
  .promo h3 {
    font-size: 20px;
  }
  .promo > span,
  .promo > .container > span {
    font-size: 15px;
  }
  .promo-uppercase h3 {
    font-size: 19px;
  }
  .promo-uppercase > span,
  .promo-uppercase > .container > span {
    font-size: 14px;
  }
}

